@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1p&display=swap');
@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1p&family=M+PLUS+2:wght@100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1p&family=M+PLUS+2:wght@100..900&family=M+PLUS+Rounded+1c&display=swap');

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

html, body {
    font-family: 'M PLUS 1p', Arial, sans-serif;
    margin: 0;
    padding: 0;
    text-align: center;
    height: 100%;
}

h1, h2, h3, h4, h5. h6 {
    font-family: 'M PlUS Rounded 1c', serif;
}

header {
    background-color: #4CAF50;
    color: white;
    padding: 0.5rem 0;
}

footer {
    background-color: #222;
    color: white;
    padding: 0.5rem 0;
    font-size: 0.8rem;
    position: relative;
    bottom: 0;
    width: 100%;
}

nav a {
    margin: 0 1rem;
    color: white;
    text-decoration: none;
}

nav a:hover {
    text-decoration: underline;
}

main {
    flex: 1;
}

.project-card {
    background: #f9f9f9;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    max-width: 600px;
    transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.project-card:hover {
    transform: scale(1.025);
    box-shadow: 0 4px 10px rgba(0,0,0,0.2);
}

/* Smooth transition between light and dark styling */
html, body {
    transition: background-color 0.25s, color 0.25s;
}

/* Dark mode styling */
@media (prefers-color-scheme: dark) {
    html, body {
        background-color: #121212;
        color: #ffffff;
    }

    header {
        background-color: #1e1e1e;
        color: white;
    }

    nav a {
        color: white;
    }

    nav a:hover {
        text-decoration: underline;
    }

    footer {
        background-color: #1e1e1e;
        color: white;
    }

    .project-card {
        background: #222;
        color: white;
        box-shadow: 0 2px 5px rgba(255, 255, 255, 0.1);
    }
}